.Accomplishments {
  font-size: 3vw; /* Default font size for larger screens */
  width: 100%;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
}

/* Media query for smaller screens (e.g., mobile devices) */
@media screen and (max-width: 480px) {
  .Accomplishments {
    font-size: 5vw; /* Increase font size for smaller screens */
  }
}



.image-container {
  position: relative;
  max-width: 80%;
  margin: 0 auto; /* Center the container horizontally */
  padding: 20px; /* Add padding as needed */
  text-align: center; /* Center the child elements horizontally */
}

.image {
  max-width: 100%; /* Ensure the image scales with its container */
  height: auto; /* Allow the height to adjust proportionally */
  width: 70%;
  object-fit: contain; /* Maintain aspect ratio and fit within the container */
  border: 2px solid #000; /* Add a border around the image */
  margin-bottom: 10px; /* Add margin for spacing */
}

/* Media query for screens with a maximum width of 768px (typical mobile devices) */
@media screen and (max-width: 768px) {
  .image {
    max-width: 90%; /* Reduce the image size for smaller screens */
  }
}

.swipeable-container {
  display: flex;
  flex-direction: column; /* Change to relative positioning */
  align-items: center;
}


.container-wrapper {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100vh;
}

.wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.swipeable-container {
  position: relative;
  height: 100%;
}

.button-container {
  position: absolute;
  top: 50%;
  width: 100%;
  display: flex;
  justify-content: space-between;
  transform: translateY(-50%);
}

.left-arrow {
  order: 1;
}

.right-arrow {
  order: 2;
}

button {
  background: rgb(0, 0, 0);
  border: 1px solid rgb(0, 0, 0);
  padding: 1rem; /* Adjust padding for button size */
  font-size: 1.5rem; /* Adjust font size for button size */
  cursor: pointer;
  color: #fff;
}

button:hover {
  background: #f0f0f0;
  color: #000;
}


@media screen and (max-width: 768px) {
  .container-wrapper {
    height: 100%; /* Adjust styles for smaller screens */
  }
}
